import React from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import './registration.css';
import '../loginform/Login.css';
export default class RegistrationForm extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};
	}
	handleSubmit = (event) => {
		event.preventDefault();
	};
	render() {
		return (
			<div className="form-container m-350">
				<h1 className="app-name">Register</h1>
				<hr class="line" />
				<Form onClick={this.handleSubmit}>
					<Form.Group controlId="data">
						<Form.Control placeholder="Enter username..." className="login-input thin-border" />
						<Form.Control placeholder="Enter email..." type="email" className="login-input thin-border" />
						<Form.Control
							placeholder="Enter password..."
							type="password"
							className="login-input thin-border"
						/>
						<Form.Control
							placeholder="Confirm password..."
							type="password"
							className="login-input thin-border"
						/>
					</Form.Group>
					<Button className="button" variant="primary" type="submit">
						Register
					</Button>
				</Form>
			</div>
		);
	}
}
